html, body {
  height: 100%;
  background-color: black;
}

body {
  background-color: #ffffff;
  font-family: Helvetica, Arial, sans-serif;
  text-align: center;
}

#root {
  height: 100%;
}

#pallete {
  height: 90%;
}

#nav {
  height: 10%;
  background-color: black;
}

button {
  padding: 5px 15px;
  font-size: 24px;
  cursor: pointer;
  margin:auto;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #fff;
  background-color: #ffffff;
  border: none;
  border-radius: 15px;
  box-shadow: 0 9px #999;
}

button:hover {
  background-color: #D9D9D9;
  color: #D9D9D9;
}

button:focus {outline:0;}

button:active {
  color:white;
  background-color: white;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}
